/* tag page names */
.namelist{
  display: inline-flex;
  flex-wrap: wrap;
}

.namelist div{
  padding: 10px;
}















/* title headline */
.tfirst{
color: rgb(255, 0, 0);
font-size: 1em;
}

.tsecond{
color: rgb(255, 255, 255);
font-size: 1em;
}

.t{
  display: flex;
  justify-content: right;
  /* font-size: xx-large; */
  font-size: 30px;
}



/* router links */
.routerlink {
  
  color: #000;
  /* outline: 1px solid wheat; */
  
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  column-gap: 5px;
  row-gap: 5px;

  margin-block: 5px;
}
.routerlink a{
  font-size: 17px;
  /* color: #000; */
  /* outline: 1px solid rgb(5, 2, 88); */
  padding-inline: 5px;
  background-color: #000;
  border-radius: 5%;
}





/* page list  */
.pagebox{
  color: #000;
  
  display: inline-flex;
  
  
  
  /* font-size: xx-large; */
  list-style: none;
  flex-wrap: wrap;
  column-gap: 3px;
  row-gap: 25px;
  /* border:1px red solid ; */
  justify-content: center;

  /* position: relative; */
  
  margin-top: 30px;
  
}

.pagebox a{
  /* border:1px #666666 solid ; */
  padding: 10px;
  background-color: #333333;
  border-radius: 20%;
  color: #eeeeee;

  
}

.activepage a{
  background-color: #666666;
  
}
