/* go page */

.gopage{
  display: flex;
  height: 30px;
  justify-content: center;
  padding: 5px;
}

.gopage input{
  border:1px #666666 solid;
  width: 60px;
  
}



/* form submission */

.sugfbar {
  
  display: flex;   
  flex-wrap: wrap;

  padding: 20px;
  
}
.sugfbar input{
  width: 100%;
  border: 2px solid #666666;
  padding: 5px;
  
}

.sugform form button{
  padding: 10px;
  border: 2px solid #666666;
  background-color: #000;
}

.showinput {
  display: inline-flex;
  column-gap: 10px;
  /* row-gap: 2px; */
  flex-wrap: wrap;
  padding-inline: 20px;
  padding-block: 10px;
}

.ticks{
  display: inline-flex;
  column-gap: 2px;
  padding-bottom: 20px;
  

}




















